
/* REGISTER PAGE CSS  */

@media screen and (max-width:768px) {
  .welcome-content h1{
    font-size: 1.5rem;
  }

  .welcome-content h6{
    display: none;
  }

  .welcome-content h5{
    font-size: 0.8rem !important;
  }
  .welcome-content img{
    height: 200px;
    width: 250px;
  }

  .form-container .form-title{
    font-size: 1.8rem !important;
  }

  .form-container input, .form-container textarea{
    width: 250px !important;
    font-size: 0.7rem !important;
  }
  .form-container button{
    font-size: 1rem !important;
  }
}

.welcome-content{
  color: #BC6C25;
}

.welcome-content h1{
  color: rgb(112, 91, 64);
}

.form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    font-family: 'Poppins', sans-serif; 
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;  
     }

  .form-container form{  
    padding: 20px;
  }

  .form-container .form-title{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 15px;
    color: rgb(112, 91, 64);
  }
  
 
  
  .form-container input, .form-container textarea{
   
    width: 300px;
    border: none;
    border-bottom: 1px solid rgb(112, 91, 64);
    border-radius: 0;
    font-size: 15px;
    font-weight: 300;
  }
  
  .form-container input:focus, .form-container textarea:focus{
    outline: none;
    border-bottom: 1px solid rgb(112, 91, 64);
  }
  
  .form-container button{
    padding: 5px 20px;
    border-radius: 5px;
    color: white;
    border: 1px solid rgb(112, 91, 64);
    background-color: rgb(112, 91, 64);
    font-size: 20px;
    font-weight: 400;
    cursor: pointer; 
    width: 100%;
  }
  
  .form-container button:hover{
    background-color: transparent;
    color: rgb(112, 91, 64);
    border: 1px solid rgb(159, 139, 113);
  }
  
