Custom styles for the card
.card {
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    /* transition: transform 0.3s ease-in-out; */
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
    box-shadow: 0px 8px 6px -6px gray;
  }
  
  /* .card:hover {
    transform: scale(1.05);
  } */
  
  .card-img-container{
    overflow: hidden;
    height: 200px;
  }
  .card-img-top {
    object-fit: cover;
    height: 100%;
    width: 100%;
    transition: transform 0.3s ease-in-out;
  }

  .card:hover .card-img-top{
    transform: scale(1.2);
  }
  
  .card-title {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    color: #333;
  }
  
  .card-text {
    color: #555;
  }
  
  .btn-outline-info{
    margin-right: 1rem !important;
  }

  .btn-outline-dark .btn-addToCart{
    
    border: 2px solid  rgb(98, 84, 66) !important;
    color:black !important;
  }

  .btn-addToCart:hover{
    background-color: rgb(98, 84, 66) !important;
  }


  .btn-outline-info, .btn-outline-dark{
    font-size: 0.817rem !important;
  }

  
  .price{
    color: rgb(47, 157, 54);
    font-weight: 600;
  }
  
@media screen and (max-width: 1200px){
  
    .card-title {
      font-size: 1rem;
    }
    .card-text {
      font-size: 0.9rem;
    }
    .btn-outline-info, .btn-outline-dark{
      font-size: 0.7rem !important;
    }
    .price{
      font-size: 0.9rem;
    }
}

@media screen and (max-width: 919px) {
  .card-img-container{
    height: 150px;
  }
  .card-title {
    font-size: 0.8rem;
  }
  .card-text {
    font-size: 0.5rem;
  }
  .btn-outline-info, .btn-outline-dark{
    font-size: 0.5rem !important;
  }

  @media screen and (max-width: 890px){
  
    .card-title {
      font-size: 0.6rem;
    }
   
    .btn-outline-info, .btn-outline-dark{
      font-size: 0.1rem !important;
      padding: 0.2rem 0.3rem;
    }
  } 
}

@media screen and (max-width: 576px){
  
  .card-title {
    font-size: 0.8rem;
  }
  .card-text {
    font-size: 0.5rem;
  }
  .btn-outline-info, .btn-outline-dark{
    font-size: 0.2rem !important;
    padding: 0.2rem 0.3rem;
  }
} 
